<template>
	<div class="shelfwrapdiv">
	<v-materialconfig ref="materialconfig" :configform="config"></v-materialconfig>
	<el-table
		:header-cell-style="{background:'#DFEBF9',color:'#606266'}"
		:loading="loading"
		:data="shelflist"
		:empty-text="$t('common.nodata')"
		border>
		<el-table-column
			prop="shelf_id"
		  :label="$t('materialshelf.shelf_id')">						
		</el-table-column>
		<el-table-column
			prop="state"
		  :label="$t('materialshelf.state')">	
		  <template slot-scope="scope">
		  <div v-if="scope.row.state==1"><span class="green"></span> <span>在线</span></div>
		  <div v-if="scope.row.state==-1"><span class="gray"></span> <span>离线</span></div>
		  <div v-if="scope.row.state==3"><span class="red"></span> <span>报警</span></div>
		  <div v-if="scope.row.state==4"><span class="blue"></span> <span>续料</span></div>
		  <div v-if="scope.row.state==8"><span class="purple"></span> <span>取料</span></div>
		  <div v-if="scope.row.state==12"><span class="black"></span> <span>库位整理</span></div>
		  <div v-if="scope.row.state==2"><span class="yellow"></span> <span>超时</span></div>
		  </template>	
		</el-table-column>
		<el-table-column
			prop="warehouse_category_name"
		  :label="$t('materialshelf.warehouse_category_name')">						
		</el-table-column>
		<el-table-column
			prop="warehouse_name"
		  :label="$t('materialshelf.warehouse_name')">						
		</el-table-column>
		<el-table-column
			prop="normal_num"
		  :label="$t('materialshelf.normal_num')">						
		</el-table-column>
		<el-table-column
			prop="out_mate_num"
		  :label="$t('materialshelf.out_mate_num')">						
		</el-table-column>
		<el-table-column
			prop="in_use_num"
		  :label="$t('materialshelf.in_use_num')">						
		</el-table-column>
		<el-table-column
			prop="type"
		  :label="$t('materialshelf.shelf_type')">						
		</el-table-column>
		<el-table-column
			prop="state"
	  	:label="$t('materialshelf.usage')">
	  	<template slot-scope="scope">
	  		<el-progress :percentage="Math.round(scope.row.in_use_num / scope.row.type * 10000) / 100.00 "></el-progress>
	  	</template>				
		</el-table-column>
		<el-table-column
			prop="state"
			:width="surpermissionFlag? '456':'120'"
	  	:label="$t('common.operation')">
	  	<template slot-scope="scope">
	  		<el-button type="primary" size="mini" @click="cancel(scope.row.shelf_id)">{{$t('materialshelf.cancelWarning')}}</el-button>
	  		<el-button type="warning" size="mini" @click="materialSet(scope.row.shelf_config)" v-if="surpermissionFlag">{{$t('materialshelf.set')}}</el-button>
	  		<el-button type="success" size="mini" @click="binding(scope.row)" v-if="surpermissionFlag">{{$t('materialshelf.bindingwarehouse')}}</el-button>
	  		<el-button type="warning" size="mini" @click="clearShelf(scope.row.shelf_id)" v-if="surpermissionFlag">{{$t('materialshelf.clearShelf')}}</el-button>
	  		<el-button type="danger" size="mini" @click="deleteShelf(scope.row.shelf_id)" v-if="surpermissionFlag">{{$t('materialshelf.deleteShelf')}}</el-button>
	  	</template>				
		</el-table-column>
	</el-table>
		<v-binding ref="binding" v-on:getShelfList="getShelfList"></v-binding>
	</div>
</template>
<script>
	import { surpermission } from '@/utils/public'
	import VMaterialconfig from './materialconfig';
	import  VBinding from './binding'
	import { shelfList, cancelWarning, recallLighted, clearShelfData, removeShelf } from '@/request/api/materialshelf';// 导入api接口
	export default{
		components:{ VMaterialconfig, VBinding },
		data() {
			return {
				loading:true,
				surpermissionFlag:surpermission(),
				shelflist:[],
				config:'',
				timer:null,
				bindingInfo:'',
			}
		},
		mounted() {
			this.getShelfList()
		},
		beforeDestroy(){
			if(this.timer) { //如果定时器还在运行 或者直接关闭，不用判断
        clearInterval(this.timer); //关闭
    	}
		},
		methods: {
			getShelfList() {
				shelfList({
				}).then(res =>{
					this.loading = false
					if(res.result){
						this.shelflist = res.data
					}else{
						this.$message({
	          	showClose: true,
	          	message: res.msg,
	          	type: 'error'
	        	})
					}
				})
			},
			stateFormatter(value){
				const Code = parseInt(value.state)
				switch (Code){
					case 1:
						return '在线';
						break;
					case -1:
						return '离线';
						break;
					case 3:
						return '报警';
						break
				}			
			},
			cancel(id){
				let _this = this
				cancelWarning({ "shelf_id":id}).then(res =>{
					if (res.result) {
						this.$message({
							showClose:true,
							message:this.$t('materialshelf.cancelWarningtips'),
							type:'success'
						})

						this.timer = window.setTimeout(function(){
						 _this.getShelfList() }, 3000);
					}else{
						this.$message({
	          	showClose: true,
	          	message: res.msg,
	          	type: 'error'
	        	})
					}
				})
			},
			materialSet(config) {
				this.config = config
				this.$refs.materialconfig.open()
			},
			binding(value){
				this.bindingInfo = value
				this.$refs.binding.open(value)
			},
			clearShelf(shelf_id){
				this.$confirm(this.$t('materialshelf.cleartips'), this.$t('materialshelf.tips'), {
        	confirmButtonText: this.$t('common.sure'),
        	cancelButtonText: this.$t('common.cancel'),
        	type: 'warning'
      	}).then(() => {
	        clearShelfData({shelf_id:shelf_id}).then(res =>{
	          if (res.result) {
	          	this.$message({
		          	showClose: true,
		          	message: this.$t('materialshelf.clearsuccess'),
		          	type: 'success'
		        	})
	            this.getShelfList()
	          }else{
	          	this.$message({
		          	showClose: true,
		          	message: res.msg,
		          	type: 'error'
		        	})
	          }
	        })
	      }).catch(() => {
	         
	      });
			},
			deleteShelf(shelf_id){
				this.$confirm(this.$t('materialshelf.deletetips'), this.$t('materialshelf.tips'), {
        	confirmButtonText: this.$t('common.sure'),
        	cancelButtonText: this.$t('common.cancel'),
        	type: 'warning'
      	}).then(() => {
	        removeShelf({shelf_id:shelf_id}).then(res =>{
	          if (res.result) {
	          	this.$message({
		          	showClose: true,
		          	message: this.$t('materialshelf.deletesuccess'),
		          	type: 'success'
		        	})
	            this.getShelfList()
	          }else{
	          	this.$message({
		          	showClose: true,
		          	message: res.msg,
		          	type: 'error'
		        	})
	          }
	        })
	      }).catch(() => {
	         
	      });
			}
		}
	}
</script>
<style>
	.shelfwrapdiv{
		margin:20px 0px;
	}
</style>


