<template>
	<div>
		<el-dialog
    :title="$t('materialshelf.bindingwarehouse')"
    :visible.sync="bindingVisible"
    width="446px">
    <div class="binding_div">
    	<template>
				<el-select value-key="id" v-model="bindingform.warehouse_category_id" placeholder="请选择库位类别" @change="categoryTypeChange">
		    	<el-option
			      	v-for="item in categoryTypeList"
			      	:key="item.id"
			      	:label="item.category_name"
			      	:value="item.id">										      
		    	</el-option>
				</el-select>
			</template>
    </div>
		<div class="binding_div">
			<template>
				<el-select value-key="id" v-model="bindingform.warehouse_name_id" placeholder="请选择库位">
		    	<el-option
			      	v-for="item in warehouselist"
			      	:key="item.id"
			      	:label="item.warehouse_name"
			      	:value="item.id">										      
		    	</el-option>
				</el-select>
			</template>
		</div>
			
			<div class="binding_div">
				<el-button @click="cancel">取消</el-button><el-button type="primary" @click="save">保存</el-button>
			</div>
		</el-dialog>
	</div>
</template>
<script>
	import { searchWarehouseCategory, searchWarehouseNameByCategory, shelfBoundWarehouseName } from '@/request/api/materialshelf';// 导入api接口
	export default{
		// props:['bindingInfo'],
		data(){
			return{
				categoryTypeList:[],
				warehouselist:[],
				bindingVisible:false,
				bindingform:{
					shelf_id:'',
					warehouse_category_id:0,
					warehouse_name_id:0,
				}

			}
		},
		methods:{
			open(value){
				this.getCategoryType()
				this.bindingVisible = true
				this.bindingform.shelf_id = value.shelf_id
				this.bindingform.warehouse_category_id = value.warehouse_category_id
				this.bindingform.warehouse_name_id = value.warehouse_name_id
				this.getWarehouseList()
				
			},
			// 获取仓库类型
	    getCategoryType(){
	    	searchWarehouseCategory({}).then(res =>{
	    		if (res.result) {
	    			this.categoryTypeList = res.data_list
	    		}
	    	})
	    },
	    // 获取库位
	    getWarehouseList(){
	    	searchWarehouseNameByCategory({warehouse_category_id:this.bindingform.warehouse_category_id}).then(res =>{
	    		if (res.result) {
	    			this.warehouselist = res.data
	    		}
	    	})
	    },
	    categoryTypeChange(){
	    	this.bindingform.warehouse_name_id = ''
	    	this.getWarehouseList()
	    },
	    cancel(){
	    	this.bindingVisible = false
	    },
	    save(){
	    	shelfBoundWarehouseName(this.bindingform).then(res =>{
	    		if (res.result) {
	    			this.bindingVisible = false
	    			this.$emit('getShelfList')
	    		} else {
	    			this.$message({
	          	showClose: true,
	          	message: res.msg,
	          	type: 'error'
	        	})
	    		}
	    	})
	    }
		}
	}
</script>
<style>
	.binding_div{
		margin-bottom:20px;
	}
</style>


