<template>
	<div>
		<el-dialog
    :title="$t('materialshelf.config')"
    :visible.sync="configVisible"
    @close="reset('configform')"
    width="720px">
		<div >
			<el-form ref="configform" :model="configform" :rules="rules" label-width="100px" :inline="true">
			  <el-form-item :label="$t('materialshelf.server_address')" prop="server_address">
			    <el-input v-model="configform.server_address"></el-input>
			  </el-form-item>
			  <el-form-item :label="$t('materialshelf.server_port')" prop="server_port">
			    <el-input v-model="configform.server_port"></el-input>
			  </el-form-item>
				<el-form-item :label="$t('materialshelf.send_info_addr')" prop="send_info_addr">
			    <el-input v-model="configform.send_info_addr" disabled></el-input>
			  </el-form-item>
			  <el-form-item :label="$t('materialshelf.default_user')" prop="default_user">
			    <el-input v-model="configform.default_user"></el-input>
			  </el-form-item>	
			  <el-form-item :label="$t('materialshelf.shelf_id')" prop="shelf_id">
			    <el-input v-model="configform.shelf_id" disabled></el-input>
			  </el-form-item>
			  <el-form-item :label="$t('materialshelf.shelf_type')" prop="shelf_type">
			    <el-input v-model="configform.shelf_type" disabled></el-input>
			  </el-form-item>			  		  
			  <el-form-item :label="$t('materialshelf.sleep_threshold_value')" prop="sleep_threshold_value">
			    <el-input v-model="configform.sleep_threshold_value"></el-input>
			  </el-form-item>
			  <el-form-item :label="$t('materialshelf.timeout_threshold_value')" prop="timeout_threshold_value">
			    <el-input v-model="configform.timeout_threshold_value"></el-input>
			  </el-form-item>
			  <el-form-item :label="$t('materialshelf.shelf_login')" prop="shelf_login">
			    <el-switch
				    v-model="configform.shelf_login"
				    active-color="#13ce66"
				    inactive-color="#ff4949"
				    :active-text="$t('materialshelf.on')"
					  :inactive-text="$t('materialshelf.off')"
				    active-value="on"
				    inactive-value="off">
				  </el-switch>
			  </el-form-item>
			  <el-form-item :label="$t('materialshelf.strict_check_mate')" prop="strict_check_mate">
			    <el-switch
					  v-model="configform.strict_check_mate"
					  active-color="#13ce66"
					  inactive-color="#ff4949"
					  active-value="on"
				    inactive-value="off"
					  :active-text="$t('materialshelf.on')"
					  :inactive-text="$t('materialshelf.off')">
					</el-switch>
			  </el-form-item>
			  <el-form-item>
			  	<el-button @click="configCancel('configform')">{{$t('common.cancel')}}</el-button>
			  	<el-button type="primary" @click="configSave('configform')">{{$t('common.save')}}</el-button>
			  </el-form-item>
	  	</el-form>
	</div>
	</el-dialog>
	</div>
</template>
<script>
	import { updateShelfConfig } from '@/request/api/materialshelf';// 导入api接口
	export default {
		props:['configform'],
		data() {
			return {
				configVisible:false,
				rules:{
					server_address:[
						{ required: true, message: this.$t('materialshelf.server_address_tips'), trigger: 'blur' },
						{
			        validator: (rule, value, callback) => {
			        	let reg = /((2(5[0-5]|[0-4]\d))|[0-1]?\d{1,2})(\.((2(5[0-5]|[0-4]\d))|[0-1]?\d{1,2})){3}/g
			          if (reg.test(value)==false) {
			            callback(new Error(this.$t('materialshelf.server_address_tips')));
			          }else{
			          	callback();
			          }
			        }, trigger: 'blur'
				    }
					],
					server_port:[
						{ required: true, message: this.$t('materialshelf.server_port_tips'), trigger: 'blur' },
						{
							validator: (rule, value, callback) =>{
								let reg =  /^([0-9]|[1-9]\d|[1-9]\d{2}|[1-9]\d{3}|[1-5]\d{4}|6[0-4]\d{3}|65[0-4]\d{2}|655[0-2]\d|6553[0-5])$/
								if (reg.test(value)==false) {
			            callback(new Error(this.$t('materialshelf.server_port_tips')));
			          }else{
			          	callback();
			          }
							}, trigger: 'blur'
						}
					],
					sleep_threshold_value:[
						{ required: true, message: this.$t('materialshelf.yuzhi_tips'), trigger: 'blur' },
						{
							validator: (rule, value, callback) =>{
								let reg =/^[1-9]\d*$/
								if (reg.test(value)==false) {
			            callback(new Error(this.$t('materialshelf.yuzhi')));
			          }else{
			          	callback();
			          }
							}, trigger: 'blur'
						}
					],
					timeout_threshold_value:[
						{ required: true, message: this.$t('materialshelf.yuzhi_tips'), trigger: 'blur' },
						{
							validator: (rule, value, callback) =>{
								let reg =/^[1-9]\d*$/
								if (reg.test(value)==false) {
			            callback(new Error(this.$t('materialshelf.yuzhi')));
			          }else{
			          	callback();
			          }
							}, trigger: 'blur'
						}
					],
				}
			}
		},
		methods: {
			open(config){
				this.configVisible = true
			},
			reset(configform){
				this.$refs.configform.resetFields()
			},
			configCancel(configform){
				this.$refs.configform.resetFields()
				this.configVisible = false
			},
			configSave(configform){
				updateShelfConfig({shelf_id:this.configform.shelf_id,shelf_config:this.configform}).then(res =>{
					if(res.result){
						this.configVisible = false;
						this.$message({
	          	showClose: true,
	          	message: 'success',
	          	type: 'success'
	        	})
					}else{
						this.$message({
	          	showClose: true,
	          	message: res.msg,
	          	type: 'error'
	        	})
	        }
	      })
			}
		}
	}
</script>


